import JXObject from "jx/core/JXObject";
import Common from "jx/editor/editionitems/Common";
let line = `
<div>
	<hr>
	<div id="id"></div>
</div>
`;
class Separator extends JXObject {
    constructor(controller, params) {
        super();
        this.controller = controller;
        this.commonLib = new Common({ jx: this.jx });
        var item = $(line);
        params.id = params.id || "";
        if (params.id.trim() != "") {
            item.find("#id").html("<b>" + params.id + " : </b></br></br>");
        }
        ;
        this.item = item[0];
    }
}
export default Separator;
